/* Please do not use this import in production. You could download this font from here https://fontstorage.com/font/playfair-display */
@import "http://fonts.fontstorage.com/import/playfair.css";
/*
	Playfair Display, 400, 400 italic, 500, 500 italic, 600, 600 italic, 700, 700 italic, 800, 800 italic, 900, 900 italic
*/
/* Please do not use this import in production. You could download this font from here https://fontstorage.com/font/open-sans */
@import "http://fonts.fontstorage.com/import/opensans.css";
/*
	Open Sans, 300, 300 italic, 400, 400 italic, 600, 600 italic, 700, 700 italic, 800, 800 italic
*/
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&family=Playfair+Display:wght@400;500;600;700&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* box-sizing fix */
/* stylelint-disable-next-line no-duplicate-selectors */
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* root font size */
:root {
  font-size: 1em;
}

/* universal max-width rule for images */
img {
  max-width: 100%;
}

/* remove default focus styles */
a, button, input {
  outline: 0;
}

a::-moz-focus-inner, button::-moz-focus-inner, input::-moz-focus-inner {
  outline: 0;
  border: 0;
}

@font-face {
  font-family: 'swiper-icons';
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}

:root {
  --swiper-theme-color: #007aff;
}

.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto;
}

.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */
.swiper-container-3d {
  perspective: 1200px;
}

.swiper-container-3d .swiper-wrapper,
.swiper-container-3d .swiper-slide,
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom,
.swiper-container-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-container-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */
.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

/**
 * Styles for the page block
 */
.page {
  font-family: Helvetica, Arial, sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.2;
}

.page__container {
  padding: 0 1em;
}

@media (min-width: 48em) {
  .page__container {
    width: 46em;
    margin: 0 auto;
  }
}

@media (min-width: 62em) {
  .page__container {
    width: 60em;
  }
}

@media (min-width: 75em) {
  .page__container {
    width: 73em;
  }
}

/**
 * Styles for the slider block.
 */
.swiper-container {
  width: 100%;
  height: 100%;
  margin-top: 142px;
}

.slider_swiper-container {
  width: 100%;
}

.slider .swiper-slide {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 148px;
}

.slider .swiper-slide h2 {
  width: 1118px;
  margin-bottom: 6px;
  color: #ffffff;
  font-family: "Playfair Display";
  font-size: 80px;
  font-weight: 400;
  line-height: normal;
  text-align: center;
  font-style: normal;
  letter-spacing: 0.4px;
  white-space: nowrap;
}

.slider .swiper-slide p {
  width: 757px;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 30px;
  font-weight: 300;
  font-style: normal;
  line-height: 48px;
  text-align: center;
  letter-spacing: 0.1px;
  margin-bottom: 37px;
  white-space: nowrap;
}

.slider .swiper-slide a {
  display: inline-block;
  padding: 17px 25px;
  border-radius: 25px;
  background-color: #eecd31;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 18px;
  font-weight: 400;
  text-align: left;
  text-transform: uppercase;
  font-style: normal;
  letter-spacing: 0.09px;
  line-height: normal;
  text-decoration: none;
  margin-bottom: 25px;
}

.slider .swiper-pagination {
  display: flex;
  justify-content: center;
}

.slider .swiper-pagination-bullet {
  background-color: grey;
  width: 10px;
  height: 10px;
  margin: 5px;
  border-radius: 20px;
  cursor: pointer;
}

.slider .swiper-pagination-bullet-active {
  background-color: white;
}

.slider .swiper-button-prev {
  background-image: url(../img/arrow_left.svg);
  width: 23px;
  height: 42px;
  position: absolute;
  left: 30px;
  top: 57%;
  z-index: 1;
  cursor: pointer;
}

.slider .swiper-button-next {
  background-image: url(../img/arrow_right.svg);
  width: 23px;
  height: 42px;
  position: absolute;
  right: 14px;
  top: 57%;
  z-index: 1;
  cursor: pointer;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 15px 0px 28px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  width: 100%;
  color: white;
  background: transparent;
}

.header-nav {
  display: flex;
  margin-left: -12px;
}

.header-nav > * {
  margin-left: 36px;
}

.logo {
  font-family: "Open Sans";
  font-size: 33px;
  font-weight: 600;
  text-align: left;
  font-style: normal;
  letter-spacing: 2.6px;
  line-height: normal;
}

li {
  font-family: "Open Sans";
  font-size: 14px;
  font-weight: 600;
  font-style: normal;
  letter-spacing: normal;
  line-height: normal;
  text-align: left;
  letter-spacing: 0.07px;
  line-height: normal;
}

section {
  position: relative;
  background-color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 30px 0;
}

body, html {
  height: 100%;
}

a {
  text-decoration: none;
  color: white;
}
